<template>
  <div class="black-bg" v-if="모달창열렸니 == true">
    <div class="white-bg">
      <img :src="원룸들[누른거].image" />
      <h4>{{ 원룸들[누른거].title }}</h4>
      <p>{{ 원룸들[누른거].content }}</p>
      <p>{{ month }}개월 선택함 : {{ 원룸들[누른거].price * month }}원</p>
      <!-- <button @click="모달창열렸니 = false">닫기</button> -->
      <!--
		props는 read-only이기 때문에 받아온 것을 수정하면 오류가 남
	-->
      <!-- <input @input="month = $event.target.value" /> -->
      <!--
		$event 는 자바스크립트 함수의 파라미터와 똑같은 역할을 함
		$event.target.value == e.target.value
	-->
      <input v-model.number="month" />
      <!--
		사용자가 <input>에 입력한 것은 전부 문자자료형으로 저장됨
		- 초기값을 숫자(0,1..)로 설정해놔도 문자로 저장됨
		:: 자바스크립트가 자료형에 좀 자유로운 편이라 곱셈은 계산이 가능함
		- 하지만 덧셈은 문자자료형으로 인식하여 이상하게 적용됨
		v-model.number="month" 로 입력
	-->

      <textarea v-model="month"></textarea>
      <select v-model="month">
        <option>1</option>
        <option>2</option>
        <option>3</option>
      </select>
      <input type="checkbox" v-model="month" /><!--true/false 로 저장됨-->

      <button @click="closeBtn()">닫기</button>
    </div>
  </div>
</template>
<script>
export default {
  name: 'modal',
  data() {
    return {
      month: 1, //초기값 설정
    };
  },
  watch: {
    //data 감시하는 함수
    //데이터 감시하려면 watch:{감시할데이터(){}}
    month(a) {
      //month 라는 데이터가 변할 때 마다 여기있는 코드 실행됨
      //   사용자가 month를 글자로 입력하면 경고문 띄워주셈
      a; //month 데이터임
      //   사용자가 month 에 입력한데이터가 13보다 크면 경고문띄우기
      if (a > 13) {
        alert('13이상 입력하지 마셈');
        this.month = 13;
      }
      //   } else if ((a = String)) {
      //     alert('글자입력하지 마셈');
      //     this.month = 1;
      //     // return false;
      //   }

      /*
	 	month(a,b) a : 변경 후 데이터
	 	month(a,b) b : 변경 전 데이터
	  */
    },
  },
  props: {
    /*
		props 받은거 등록
		- props : {데이터이름 : 자료형 이름}
	*/
    원룸들: Array,
    누른거: Number,
    모달창열렸니: Boolean,
    /*
		props는 read-only이기 때문에 받아온 것을 수정하면 오류가 남
	*/
  },
  methods: {
    closeBtn() {
      this.$emit('closeBtn');
    },
  },
  beforeUpdate() {
    if (this.month == 2) {
      alert('no 2');
    }
  },
};
</script>
<style></style>
