<template>
  <div>
    <img :src="원룸들.image" class="room-img" />
    <h4 class="red" :style="스타일" @click="send()">
      <!--
		custom event
		- 이걸로 부모데이터 수정해야됨
		(실은 데이터 수정해달라는 메세지임)
		------------
		*** $어쩌구는 Vue만의 특별한 변수 ($emit, $event...)

		@click="$emit('작명', 데이터)"

	-->
      {{ 원룸들.title }}
    </h4>
    <!--
      html 속성에 데이터 바인딩 하고 싶을 때, 
      :속성 = "데이터 이름"
    -->
    <p>{{ 원룸들.price }}원</p>
  </div>
</template>
<script>
export default {
  name: 'Card',
  props: {
    원룸들: Array,
  },
  methods: {
    send() {
      this.$emit('openModal', this.원룸들.id);
    },
  },
};
</script>
<style></style>
