<template>
  <!------v-if 정리 -------->
  <div v-if="1 == 2">안녕하세요</div>

  <div v-else>안녕히가세요</div>
  <!--위에 있는 v-if 가 참이 아니면 이걸 보여주세요~-->

  <!-- <div v-else-if="1 == 3">안녕하세요2</div> -->
  <!--if 문 연달아 여러개 쓰고 싶을 때 v-else-if 사용 -->
  <!---------------------------->
  <!-- <div class="start" :class="{ end: 모달창열렸니 }"> -->
  <!-- <Modal
    @closeBtn="모달창열렸니 = false"
    :원룸들="원룸들"
    :누른거="누른거"
    :모달창열렸니="모달창열렸니"
  /> -->
  <!-- </div> -->

  <transition name="fade">
    <Modal
      @closeBtn="모달창열렸니 = false"
      :원룸들="원룸들"
      :누른거="누른거"
      :모달창열렸니="모달창열렸니"
    />
  </transition>

  <!--
    props로 자식에게 데이터 보내는 법
    - 1. 데이터보내고 / 2. 등록하고 / 3. 쓰셈
    <자식 :데이터이름(작명) = "데이터이름(실제 데이터이름)">
  -->

  <div class="menu">
    <!-- <a v-for="작명 in 3" :key="작명">Home</a> -->
    <!--
      Vue의 HTML 반복문
      <태그 v-for="작명 in 몇회">
      -----
      ** 반복문 사용할 때, :key 안쓰면 에러남
    -->
    <!-- <a>Products</a>
    <a>About</a> -->
    <!------------------ array/object 집어넣기  ---------------------->
    <a v-for="(작명, 숫자) in 메뉴들" :key="숫자">{{ 작명 }}</a>
    <!--
      array/object 집어넣기 가능
      - 그럼 자료 안의 데이터 갯수만큼 반복됨
      - 그럼 작명한 변수는 데이터 안의 자료가 됨
      -------------------
      :key 의 용도
      - 반복문 쓸 때 꼭 써야함
      - 반복문 돌린 요소를 컴퓨터가 구분하기 위해 씀
      - 반복문이 돌면서 변하는 요소가 출력됨 (0,1,2... 같은 숫자)
    -->
  </div>

  <Discount v-if="showDiscount == true" :discountValue="discountValue" />

  <button @click="priceSort">가격 순 정렬</button>
  <button @click="sortBack">되돌리기</button>
  <button @click="sortReverse">가격 역순 정렬</button>
  <button @click="sortWords">가나다 순 정렬</button>

  <Card
    @openModal="
      모달창열렸니 = true;
      누른거 = $event;
    "
    :원룸들="원룸들[idx]"
    v-for="(room, idx) in 원룸들"
    :key="idx"
  />

  <!--
    custom event 부모가 메세지 수신할 땐
    - @작명한거=""
    - 자식이 openModal 이름의 메세지 보내면 자바스크립트 실행해주셈~
    ----
    - 자식이 보낸 데이터는 $event 변수에 담겨있음
  -->

  <!--
    props 보낼 때 다양한 자료형 입력가능
    <Card :데이터이름="[123,456]" />
    - :작명 = "Array, Object"

    간단한 문자열 같은 경우에는 v-bind 없이 작성해도 됨
    <Card 데이터이름="안녕하세요" /> ==> 작명 ="문자자료"
    <Card :데이터이름="123123" /> ==> :작명="숫자자료"
    - 숫자에는 v-bind를 해줘야 string이 아닌 number 로 인식이 됨
  -->
</template>

<script>
// import { apple } from './assets/oneroom.js';
import data from './assets/oneroom.js';
import Discount from './Discount.vue';
import Modal from './Modal.vue';
import Card from './Card.vue';
// data;

var 어레이 = [10, 20, 30];
어레이[1]; //20

export default {
  name: 'App',
  data() {
    //데이터 보관함
    return {
      discountValue: 3,
      showDiscount: true,
      원룸들오리지널: [...data],
      //array/object 데이터의 각각 별개의 사본을 만드려면 [...array자료]
      누른거: 0,
      원룸들: data,
      모달창열렸니: false,
      //데이터는 object 자료로 저장해두셈 {자료이름 : 자료내용}
      price1: 60,
      price2: 70,
      /*
        데이터 바인딩 하는 이유
        - html에 하드코딩 해놓으면 나중에 변경 어려움
        - vue의 실시간 자동 렌더링 사용을 하기 위해
          1. vue에서 data를 변경하면 data와 관련된 html이 자동으로 변경됨(스무스하게 변경됨)
        --------
        그러므로 자주 변할 것 같은 데이터들은
        데이터로 보관하고 html에 {{꽂아넣으셈}}
      */
      스타일: 'color:blue',
      products: ['역삼동원룸', '천호동원룸', '마포구원룸'],

      /*------------------------------------ */
      /*------------------------------------ */
      /*------------------------------------ */

      메뉴들: ['Home', 'Shop', 'About'],
      rooms: [
        { products: '역삼동원룸', price: '50만원' },
        { products: '역삼동원룸', price: '50만원' },
        { products: '역삼동원룸', price: '50만원' },
        { products: '역삼동원룸', price: '50만원' },
        { products: '역삼동원룸', price: '50만원' },
      ],
      신고수: [0, 0, 0, 0, 0],
    };
  },
  methods: {
    //Vue에서 함수만들고 싶으면 methods:{함수(){}} 안에 만드셈
    increase() {
      this.신고수 += 1;
      /*
        vue에서 함수 만들 때 주의사항
        - 함수 안에서 데이터 쓸 땐 this.데이터명
      */
    },
    priceSort() {
      this.원룸들.sort(function (a, b) {
        return a.price - b.price;
      });
      /*
        [3,5,2].sort() => 2,3,5 정렬됨
        - 문자정렬임

        ::숫자 정렬을 위해서는
        [3,5,2].sort(function(a,b){
          return a-b
        })
        - a:3, b:5
        - a-b = -2 (값이 음수면 a 를 왼쪽으로 보내주세요 로 동작)
        - 이런식으로 값들을 비교해서 숫자 정렬을 함
      */
      /*----- 참고 ------
      sort() 는 원본이 변형됨
      map(), filter() 등은 원본을 보존해줌
     */
    },
    sortBack() {
      this.원룸들 = [...this.원룸들오리지널];
      /*
        Array 주의
        - 등호로 array를 집어넣어주세요 하면
        :: 두개의 값을 공유해주세요가 되어버려서
        --> 값이 결국 같아져버림

        ==> 개별 복사본을 만들어서 넣어주기
      */
    },
    sortReverse() {
      this.원룸들.sort(function (a, b) {
        return b.price - a.price;
      });
    },
    sortWords() {
      this.원룸들.sort(); //좀 더 공부해보기
    },
  },

  created() {
    //서버에서 데이터 가져오는 코드~
  },

  // mounted() {
  //   setTimeout(() => {
  //     //arrow function을 사용해야 this 인식을 잘 해줌
  //     //일반 function을 사용하면 에러날 수 있음
  //     this.showDiscount = false;
  //   }, 2000);
  // },
  mounted() {
    setInterval(() => {
      if (this.discountValue > 0) {
        this.discountValue--;
      } else {
        this.showDiscount = false;
        return false;
      }
    }, 1000);
  },

  components: { Discount, Modal, Card },
};
</script>

<style>
.fade-enter-from {
  /* 시작 시 스타일 */
  opacity: 0;
}
.fade-enter-active {
  transition: all 0.3s ease-in-out;
}
.fade-enter-to {
  /* 끝날 때 스타일 */
  opacity: 1;
}

.fade-leave-from {
  /* 시작 시 스타일 */
  opacity: 1;
}
.fade-leave-active {
  transition: all 0.3s ease-in-out;
}
.fade-leave-to {
  /* 끝날 때 스타일 */
  opacity: 0;
}

.start {
  opacity: 0;
  transition: all 0.3s ease-in-out;
}
.end {
  opacity: 1;
}

